import React from 'react'
import { NavLink } from 'react-router-dom'
import './PaisCard.css'

export const PaisCard = ({
    _id,
    name,
    code,
    flag
}) => {
    return (
        <div className="card" style={{maxWidth: 500, maxHeight: 300}}>
            <div  className="row">
                <div className="col-md-5 card-imagen">
                    <img src={`${flag}`} className="card-img-top" width="180" height="90"alt = {code}></img>
                </div>
                <div className="col-md-7">
                    <div className="card-body">
                        <h5 className="card-title">Country:</h5>
                        <h6 className="card-title">{name}</h6>
                        <h6 className="card-title">Code: {code}</h6>
    
                    </div>
                </div>
                <NavLink to={`./pais/${name}/NI/`} className="stretched-link"></NavLink>
            </div>
        </div>
        
    )
}
